.FinishUp{
    width: 60%;
    margin: auto;
    font-family: Arial, Helvetica, sans-serif;
}
.finish-header h2{
    font-size: 30px;
    margin-bottom: 0px;
}
.finish-header p{
    margin-top: 5p;
    color: #7a7a7a;
}
.finish-body{
    margin-top: 50px;
    border-radius: 10px;
    background-color: #fafafa;
    padding: 10px;
}
.Planname{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
}
.finish-add-plan-item{
    padding: 0px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.finish-add-plan-item p{
    margin-top: 7px;
    margin-bottom: 7px;
    color: #7a7a7a;
    font-size: 14px;
}
.planprices{
    font-size: 14px;
    color: blue;
}
.finish-Step{
    margin-top: 20px;
}
.totalFinish{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 20px;
}
.totalFinish p{
    font-size: 14px;
    color: #7a7a7a;
    font-weight: bold;
}
.totalprice{
    color:blue !important;
    font-size: 16px !important;
}
@media(max-width:991px){
    .FinishUp{
        width: 95%;
        margin: auto;
    }
}