.Plancard-item{
    width: 30%;
    border: 1px solid #7a7a7a;
    border-radius: 10px;
    padding-left: 15px;
    padding-top: 10px;
    margin-bottom: 20px;
    cursor: pointer;
}
.Plancard-item:hover{
    border: 1px solid blue;
}

.Plancard-item img{
    margin-bottom: 10px;
}
.Plancard-item h4{
    margin-bottom: 5px;
}
.Plancard-item p{
    margin: 0px;
    font-size: 14px;
    color: #7a7a7a;
    margin-bottom: 5px;
}
@media(max-width:991px){
    .Plancard-item{
        width: 40%;
    }
}