.form-group{
    width: 60%;
    font-family: Arial, Helvetica, sans-serif;
}
.Info-header h1{
    margin-bottom: 0px;
}
.Info-header p{
    color: #c0c0c0;
    font-size: 14px;
}
.form-item {
    margin-bottom: 20px;
}
.form-item label{
    display: block;
    margin-bottom: 4px;
    font-size: 14px;
}
.form-item input{
    width: 70%;
    height: 35px;
    border: 1px solid #d1d1d1;
    border-radius: 10px;
    text-indent: 10px;
}
.form-item input::placeholder{
    text-indent: 10px;
}
.form-item input:focus{
    border: 1px solid #000;
    outline: 0px;
}
.btn-section{
    width: 70%;
    margin-top: 100px;
    text-align: end;
}
.btn-section button{
    background-color: blue;
    border: none;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
}
@media(max-width:991px){
    .form-group{
        width: 90%;
        margin: auto;
    }
    .form-item input{
        width: 100%;
    }
    .btn-section{
        margin: auto;
        margin-top: 40px;
        margin-bottom: 40px;
        text-align: center;
    }
    .btn-section button{
       text-align: center;
    }
}