.AddOns{
    width: 60%;
    font-family: Arial, Helvetica, sans-serif;
    margin: 0px auto;
}
.AddOns-header h2{
    font-size: 30px;
    margin-bottom: 0px;
}
.AddOns-header p{
    margin-top: 5p;
    color: #7a7a7a;
}
.AddOns-option .input-item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #000;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 0px 5px;
}
.input-item p{
    font-size: 13px;
     color: #7a7a7a;
}
.input-radio-check{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.input-radio input{
   transform: translateY(100%);
}
.input-radio p{
    display: block;
}
.input-radio span{
    display: block;
    margin-top: 20px;
     margin-left: 10px;
}
.input-radio p{
    margin-left: 35px;
    font-size: 13px;
    margin-top: 5px;
    color: #7a7a7a;
}
.input-radio span{
    font-size: 15px;
}
.AddOns-Step{
    margin-top: 140px;
}
@media(max-width:991px){
    .AddOns{
        width: 95%;
        margin: auto;
    }
    .AddOns-Step{
        margin-top: 70px;
    }
}
