.PlanSection{
    font-family: Arial, Helvetica, sans-serif;
    width: 60%;
    margin: 0px auto;
}
.PlanSection p{
    color: #7a7a7a;
    font-size: 14px;
    margin-top: 7px;
    letter-spacing: 1px;
}
.PlanSection h2{
    margin-bottom: 0px;
}
.PlanCard{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}
.PlanOption{
    margin: auto;
    width: 100%;
    background-color: #f5f5f5;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.PlanOption button{
    margin: 0px 20px;
    border: none;
    padding: 10px;
    background-color: transparent;
    cursor: pointer;
}
.Step{
    margin-top: 160px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.Step button{
    cursor: pointer;
    border: none;
    padding: 10px;
    border-radius: 10px;
}
.Step .btn_1{
    color: blue;
    background-color: white;
}
.Step .btn_2{
    background-color: blue;
    color: white;
}
@media(max-width:991px){
    .PlanSection{
        width: 95%;
        margin: auto;
    }
    .Step{
        margin-top: 50px;
        margin-bottom: 30px;
    }
}