.SideBar{
    width: 30%;
    margin: 15px;
    background-repeat: no-repeat;
    background-position-y: center;
    background-size: 100%;
    border-radius: 10px;
    height: 460px;
}


@media(max-width:991px) {
    .SideBar{
        width: 100%;
        display: flex;
        flex-direction: row;
        height: 100px;
        background-position: center;
        background-size: cover;
        margin: 0px;
    }
}