.App{
    width: 70%;
    margin: auto;
    background-color: white;
    box-shadow: 0px 2px 6px rgba(0,0,0,0.1);
    transform: translateY(10%);
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
@media(max-width:991px){
    .App{
        display: flex;
        flex-direction: column;
    }
}