.Side-line{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 20px;
}

.Side-line-num{
    border: 1px solid #CCC;
    padding: 6px 12px;
    border-radius: 50%;
    margin-left: 20px;
    font-size: 14px;
    color: white;
}
.side-line-step{
    width: 70%;
    font-size: 14px;
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-line-step p:first-of-type{
    font-size: 11px;
    color: #d1d1d1;
    margin-bottom: 2px;
}
.side-line-step p:last-of-type{
    font-size: 13px;
}
.side-line-step p{
    margin-bottom: 0px;
    margin-top: 0px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}
@media(max-width:991px){
    .Side-line{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        width: 100%;
    }
    .side-line-step{
        margin-top: 1%;
        margin-left: 0px;
    }
    .side-line-step p:first-of-type{
        font-size: 10px;
        text-align: center;
    }
    .side-line-step p:last-of-type{
        font-size: 10px;
        text-align: center;
    }
    .Side-line-num{
        margin-left: 0px;
    }
}
@media(max-width:600px){
    .side-line-step p:last-of-type{
        display: none;
    }
}